.filter-box ul {
  margin: 0;
  padding: 0;
}
.filter-box ul li {
  list-style: none;
}
.filter-box .search-box .property-header {
  width: 100px;
  display: inline-block;
}
.filter-box .filter-property .property-header {
  width: 100px;
  display: inline-block;
}
.filter-box .filter-property ul {
  display: inline-block;
}
.filter-box .filter-property ul li {
  display: inline-block;
  padding: 0 8px;
}
.filter-box .property-box .property-item {
  position: relative;
}
.filter-box .property-box .property-item .property-header {
  position: absolute;
  left: 0;
  top: 0;
}
.filter-box .property-box .property-item .property-body {
  margin: 0 100px;
}
.filter-box .property-box .property-item .property-body li {
  display: inline-block;
  padding: 0 8px;
}
.filter-box .property-box .property-item .property-body li.active {
  background: #f00;
  color: #fff;
}
.filter-box .property-box .property-item .property-action {
  position: absolute;
  right: 0;
  top: 0;
}
.filter-box .sort-box span {
  padding: 0 8px;
  display: inline-block;
}
.filter-box .sort-box span.active {
  background: #f00;
  color: #fff;
}
.filter-box .sort-box span.order-asc i, .filter-box .sort-box span.order-desc i {
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
}
.filter-box .sort-box span.order-asc i:before {
  content: "\f15d";
}
.filter-box .sort-box span.order-desc i:before {
  content: "\f15e";
}